class Game {
  constructor(canvas, ctx) {
    this.canvas = canvas;
    this.ctx = ctx;
    this.x = 0;
    this.y = 0;
    this.mx = 0;
    this.my = 2;
  }

  draw() {
    if (this.y > this.canvas.height - 20) {
      this.y = 0;
    }

    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.ctx.beginPath();
    this.ctx.rect(this.x, this.y, 45, 30);
    this.ctx.fillStyle = "#00BFA5";
    this.ctx.fill();
    this.ctx.closePath();

    this.ctx.beginPath();
    this.ctx.moveTo(0, this.canvas.height - 20);
    this.ctx.lineTo(this.canvas.width, this.canvas.height - 20);
    this.ctx.strokeStyle="#00BFA5";
    this.ctx.stroke();

    this.x += this.mx;
    this.y += this.my;
  }
}

module.exports = Game;



